import Imageplatform from "../images/platform.png";
import background from "../images/background.png";
import hills from "../images/hills.png";
import platformSmallTall from "../images/platformSmallTall.png";
import FlagImage from "../images/flag.png";
import spriteRunLeft from "../images/spriteRunLeft.png";
import spriteRunRight from "../images/spriteRunRight.png";
import spriteStandLeft from "../images/spriteStandLeft.png";
import spriteStandRight from "../images/spriteStandRight.png";

let canvas = document.querySelector("#canvas");
let success = document.querySelector(".success");

const c = canvas.getContext("2d");
canvas.width = 1024;
canvas.height = 576;

const gravity = 1;

class Palyer {
  constructor() {
    this.position = {
      x: 100,
      y: 400,
    };
    this.velocity = {
      x: 0,
      y: 0,
    };
    this.width = 66;
    this.height = 150;
    this.speed = 10;
    this.Image = CreateImage(spriteStandRight);
    this.frames = 0;
    this.Sprite = {
      Stand: {
        right: CreateImage(spriteStandRight),
        left: CreateImage(spriteStandLeft),
        CropWidth: 177,
        width: 66,
      },
      Run: {
        right: CreateImage(spriteRunRight),
        left: CreateImage(spriteRunLeft),
        CropWidth: 340,
        width: 127.875,
      },
    };
    this.currentSprite = this.Sprite.Stand.right;
    this.CurrentCropWidth = 177;
  }
  darw() {
    c.drawImage(
      this.currentSprite,
      this.CurrentCropWidth * this.frames,
      0,
      this.CurrentCropWidth,
      400,
      this.position.x,
      this.position.y,
      this.width,
      this.height
    );
  }

  update() {
    this.frames++;
    if (
      this.frames > 59 &&
      (this.currentSprite === this.Sprite.Stand.right ||
        this.currentSprite === this.Sprite.Stand.left)
    )
      this.frames = 0;
    else if (
      this.frames > 29 &&
      (this.currentSprite === this.Sprite.Run.right ||
        this.currentSprite === this.Sprite.Run.left)
    )
      this.frames = 0;

    this.darw();
    this.position.y += this.velocity.y;
    this.position.x += this.velocity.x;
    if (this.position.y + this.height + this.velocity.y <= canvas.height) {
      this.velocity.y += gravity;
    } else {
      this.velocity.y = 0;
    }
  }
}

class Platform {
  constructor({ x, y, Image }) {
    this.position = {
      x,
      y,
    };
    this.width = Image.width;
    this.height = Image.height;
    this.Image = Image;
  }
  darw() {
    c.drawImage(this.Image, this.position.x, this.position.y);
  }
}

class GenericObject {
  constructor({ x, y, Image }) {
    this.position = {
      x,
      y,
    };
    this.width = Image.width;
    this.height = Image.height;
    this.Image = Image;
  }
  darw() {
    c.drawImage(
      this.Image,
      this.position.x,
      this.position.y,
      this.width,
      this.height
    );
  }
}

function CreateImage(ImageSrc) {
  const image = new Image();
  image.src = ImageSrc;
  return image;
}

let ImageplatformSrc = CreateImage(Imageplatform);

let player = new Palyer();
let platforms = [];
let GenericObjects = [];

let keys = {
  right: {
    pressed: false,
  },
  left: {
    pressed: false,
  },
};

let scrollOffset = 0;

function init() {
  ImageplatformSrc = CreateImage(Imageplatform);
  player = new Palyer();
  player.position.y = 270;
  platforms = [
    new Platform({
      x: ImageplatformSrc.width * 4 + 590,
      y: 270,
      Image: CreateImage(platformSmallTall),
    }),
    new Platform({
      x: ImageplatformSrc.width * 5 + 600,
      y: 270,
      Image: CreateImage(platformSmallTall),
    }),
    new Platform({
      x: ImageplatformSrc.width * 8 + 594,
      y: 270,
      Image: CreateImage(platformSmallTall),
    }),
    new Platform({ x: 0, y: 480, Image: ImageplatformSrc }),
    new Platform({
      x: ImageplatformSrc.width + 100,
      y: 480,
      Image: ImageplatformSrc,
    }),
    new Platform({
      x: ImageplatformSrc.width * 2 + 300,
      y: 480,
      Image: ImageplatformSrc,
    }),
    new Platform({
      x: ImageplatformSrc.width * 3 + 500 - 2,
      y: 480,
      Image: ImageplatformSrc,
    }),
    new Platform({
      x: ImageplatformSrc.width * 4 + 300 - 2,
      y: 480,
      Image: ImageplatformSrc,
    }),
    new Platform({
      x: ImageplatformSrc.width * 5 + 400 - 2,
      y: 480,
      Image: ImageplatformSrc,
    }),
    new Platform({
      x: ImageplatformSrc.width * 6 + 400 - 2,
      y: 480,
      Image: ImageplatformSrc,
    }),
    new Platform({
      x: ImageplatformSrc.width * 7 + 400 - 2,
      y: 480,
      Image: ImageplatformSrc,
    }),
    new Platform({
      x: ImageplatformSrc.width * 8 + 600 - 2,
      y: 480,
      Image: ImageplatformSrc,
    }),
    new Platform({
      x: ImageplatformSrc.width * 9 + 800 - 2,
      y: 480,
      Image: ImageplatformSrc,
    }),
    new Platform({
      x: ImageplatformSrc.width * 10 + 1000 - 2,
      y: 480,
      Image: ImageplatformSrc,
    }),
    new Platform({ x: 7300, y: 320, Image: CreateImage(FlagImage) }),
  ];

  let BackgroundLength = 11640;
  let hillslength = 11640;
  GenericObjects = [
    new GenericObject({
      x: 0,
      y: 0,
      Image: CreateImage(background),
    }),
    new GenericObject({
      x: 0,
      y: 0,
      Image: CreateImage(hills),
    }),
    new GenericObject({
      x: BackgroundLength,
      y: 0,
      Image: CreateImage(background),
    }),
    new GenericObject({
      x: hillslength,
      y: 0,
      Image: CreateImage(hills),
    }),
    new GenericObject({
      x: BackgroundLength * 2,
      y: 0,
      Image: CreateImage(background),
    }),
    new GenericObject({
      x: hillslength * 2,
      y: 0,
      Image: CreateImage(hills),
    }),
    new GenericObject({
      x: BackgroundLength * 3,
      y: 0,
      Image: CreateImage(background),
    }),
    new GenericObject({
      x: hillslength * 3,
      y: 0,
      Image: CreateImage(hills),
    }),
    new GenericObject({
      x: BackgroundLength * 4,
      y: 0,
      Image: CreateImage(background),
    }),
    new GenericObject({
      x: hillslength * 4,
      y: 0,
      Image: CreateImage(hills),
    }),
  ];
  keys = {
    right: {
      pressed: false,
    },
    left: {
      pressed: false,
    },
  };
  scrollOffset = 0;
}

function animate() {
  let FinalNumber = 102000;
  let NumMoveing = player.speed;
  requestAnimationFrame(animate);
  c.clearRect(0, 0, canvas.width, canvas.height);

  GenericObjects.forEach((GenericObject) => {
    GenericObject.darw();
  });

  player.update();
  platforms.forEach((platform) => {
    platform.darw();
    if (keys.right.pressed && player.position.x < 400) {
      player.velocity.x = NumMoveing;
    } else if (keys.left.pressed && player.position.x > 100) {
      player.velocity.x = -NumMoveing;
    } else {
      player.velocity.x = 0;
      if (keys.right.pressed) {
        if (scrollOffset <= FinalNumber) {
          scrollOffset += NumMoveing;
          platform.position.x -= NumMoveing;
          GenericObjects.forEach((GenericObject) => {
            GenericObject.position.x -= 5;
          });
        }
      }
      if (keys.left.pressed) {
        if (scrollOffset > 0) {
          scrollOffset -= NumMoveing;
          platform.position.x += NumMoveing;
          GenericObjects.forEach((GenericObject) => {
            GenericObject.position.x += 5;
          });
        }
      }
    }
    if (
      player.position.y + player.height <= platform.position.y &&
      player.position.y + player.height + player.velocity.y >=
        platform.position.y &&
      player.position.x + player.width >= platform.position.x &&
      player.position.x <= platform.position.x + platform.width
    ) {
      player.velocity.y = 0;
    }
  });
  if (scrollOffset > FinalNumber) {
    console.log("You Win");
    success.classList.add("active");
  } else {
    success.classList.remove("active");
  }
  if (player.position.y + player.height >= canvas.height) {
    init();
  }
}

animate();

addEventListener("keyup", ({ keyCode }) => {
  switch (keyCode) {
    case 40: {
      // console.log("Down");
      break;
    }
    case 38: {
      // console.log("Top");
      if (player.position.y >= 0) {
        player.velocity.y -= 10;
      }

      break;
    }
    case 39: {
      // console.log("Right");
      keys.right.pressed = false;
      player.currentSprite = player.Sprite.Stand.right;
      player.CurrentCropWidth = player.Sprite.Stand.CropWidth;
      player.width = player.Sprite.Stand.width;
      break;
    }
    case 37: {
      // console.log("Left");
      keys.left.pressed = false;
      player.currentSprite = player.Sprite.Stand.left;
      player.CurrentCropWidth = player.Sprite.Stand.CropWidth;
      player.width = player.Sprite.Stand.width;
      break;
    }
  }
});
addEventListener("keydown", ({ keyCode }) => {
  switch (keyCode) {
    case 40: {
      // console.log("Down");
      break;
    }
    case 38: {
      // console.log("Top");
      if (player.position.y >= 0) {
        player.velocity.y -= 10;
      }
      break;
    }
    case 39: {
      // console.log("Right");
      keys.right.pressed = true;
      player.currentSprite = player.Sprite.Run.right;
      player.CurrentCropWidth = player.Sprite.Run.CropWidth;
      player.width = player.Sprite.Run.width;
      break;
    }
    case 37: {
      // console.log("Left");
      keys.left.pressed = true;
      player.currentSprite = player.Sprite.Run.left;
      player.CurrentCropWidth = player.Sprite.Run.CropWidth;
      player.width = player.Sprite.Run.width;
      break;
    }
  }
});
